@keyframes spin {
    to {
        transform: rotate(360deg);
    }
    
}

.spinner {
    position: fixed;
    top: calc(50% - 40px);
    left: calc(50% - 40px);
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 2px solid black;
    border-top: none;
    border-bottom: none;
    animation: spin 1.5s infinite linear;
    z-index: 999;
}
